<template>
  <div >
    <van-cell  icon="search" v-for="(item,index) in list" :key="index" @click="$emit('search',item)">
    <span v-html="highLight(item)" slot="title"></span>
    </van-cell>
  </div>
</template>

<script>
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import 《组件名称》 from '《组件路径》';
import { getSuggestion } from '@/api/search.js'
// import { debounce } from 'lodash'
// 方式3-防抖,手写能够及格防抖

function debounce (callback, num) {
  // 闭包,延长的timer的生命周期
  let timer = null
  // 高阶函数,一个函数返回另外一个函数
  return function (val) {
    const that = this
    if (timer) clearTimeout(timer)
    timer = setTimeout(() => {
      // 使用call/bind/apply改变this执行
      callback.call(that, val)
    }, num)
  }
}

export default {
// import引入的组件需要注入到对象中才能使用
  name: 'SearchSuggestion',
  props: {
    searchText: {
      type: String,
      required: true
    }
  },
  components: {},
  data  () {
    // 这里存放数据
    return {
      list: [],
      timer: null
    }
  },
  // 监听属性 类似于data概念
  computed: {},

  // 方法集合
  methods: {
    highLight (item) {
      return item.replace(new RegExp(this.searchText, 'gi'),
        '<span style="color:#3296fa;">' + this.searchText + '</span>')
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  async created  () {
    // const res = await getSuggestion(this.searchText)
    // this.list = res.data.data.options
    // console.log(res)
  },
  // 监控data中的数据变化
  watch: {
    searchText: {
      // 方式1-防抖,手写防抖(不及格版本)
      /*  handler (val) {
        if (this.timer) clearTimeout(this.timer)
        this.timer = setTimeout(async () => {
          const res = await getSuggestion(val)
          this.list = res.data.data.options
        }, 500)
      }, */

      // 方式2-防抖,使用工具
      handler: debounce(async function (val) {
        const res = await getSuggestion(val)
        if (res.data.data.options[0] === null) {
          this.list = []
        } else {
          this.list = res.data.data.options
        }
      }, 500),
      immediate: true
    }
  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted  () {

  },
  beforeCreate  () {}, // 生命周期 - 创建之前
  beforeMount  () {}, // 生命周期 - 挂载之前
  beforeUpdate  () {}, // 生命周期 - 更新之前
  updated  () {}, // 生命周期 - 更新之后
  beforeDestroy  () {}, // 生命周期 - 销毁之前
  destroyed  () {}, // 生命周期 - 销毁完成
  activated  () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang='less' scoped>
// @import url(); 引入公共css类
</style>
