<!--  -->
<template>
  <div class="search-container">
    搜索页面
    <form action="/">
      <van-search
        v-model="value"
        placeholder="请输入搜索关键字"
        show-action
        background="#3296FA"
        @search="onSearch"
        @cancel="onCancel"
        @focus="isResultShow=false"
      ></van-search>
    </form>
    <search-result v-if="isResultShow" :search-text="value"></search-result>
    <search-suggestion @search="onSearch" :search-text="value" v-else-if="value"></search-suggestion>
    <search-history :searchHistories="searchHistories" v-else></search-history>
  </div>
</template>

<script>
import SearchHistory from './components/search-history.vue'
import SearchResult from './components/search-result.vue'
import SearchSuggestion from './components/search-suggestion.vue'

// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import 《组件名称》 from '《组件路径》';

export default {
// import引入的组件需要注入到对象中才能使用
  name: 'SearchPage',
  props: {},
  // eslint-disable-next-line vue/no-unused-components
  components: { SearchSuggestion, SearchResult, SearchHistory },
  data  () {
    // 这里存放数据
    return {
      value: '',
      searchText: '',
      isResultShow: false, // 控制搜索结果的显示
      searchHistories: JSON.parse(localStorage.searchHistories || '[]')
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {
    searchHistories (val) {
      localStorage.searchHistories = JSON.stringify(val)
    }
  },
  // 方法集合
  methods: {
    // 输入框,点击回车
    onSearch (val) {
      console.log(val)
      // 储存value
      this.value = val
      const index = this.searchHistories.findIndex(t => t === val)
      if (index !== -1) this.searchHistories.splice(index, 1)
      this.searchHistories.unshift(val)
      // 显示搜索结果组件
      this.isResultShow = true
    },
    onCancel () {
      console.log('取消')
      this.$router.back()
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created  () {

  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted  () {

  },
  beforeCreate  () {}, // 生命周期 - 创建之前
  beforeMount  () {}, // 生命周期 - 挂载之前
  beforeUpdate  () {}, // 生命周期 - 更新之前
  updated  () {}, // 生命周期 - 更新之后
  beforeDestroy  () {}, // 生命周期 - 销毁之前
  destroyed  () {}, // 生命周期 - 销毁完成
  activated  () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang='less' scoped>
// @import url(); 引入公共css类
.search-container {
  .van-search__action {
    color: #fff;
  }
}
</style>
