<!--  -->
<template>
<div class=''>
    <van-list
    v-model="loading"
    :finished="finished"
    finished-text="没有更多了"
    @load="onLoad"
    >
        <van-cell v-for="(item,index) in list" :key="index"
        :title="item.title"
        ></van-cell>
    </van-list>
</div>
</template>

<script>
import { getResult } from '@/api/search'
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import 《组件名称》 from '《组件路径》';

export default {
// import引入的组件需要注入到对象中才能使用
  name: 'SearchResult',
  props: {
    searchText: {
      type: String,
      required: true
    }
  },
  components: {},
  data  () {
    // 这里存放数据
    return {
      list: [],
      loading: false,
      finished: false,
      page: 1
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {},
  // 方法集合
  methods: {
    async onLoad () {
      // 1.发送请求
      const res = await getResult({
        page: this.page,
        per_page: 10,
        q: this.searchText
      })
      // 2.追加数据
      this.list.push(...res.data.data.results)
      this.page++
      // 3.改变状态值
      this.loading = false
      // 4.判断是否结束
      if (res.data.data.results.length === 0) {
        this.finished = true
      }
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created  () {

  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted  () {

  },
  beforeCreate  () {}, // 生命周期 - 创建之前
  beforeMount  () {}, // 生命周期 - 挂载之前
  beforeUpdate  () {}, // 生命周期 - 更新之前
  updated  () {}, // 生命周期 - 更新之后
  beforeDestroy  () {}, // 生命周期 - 销毁之前
  destroyed  () {}, // 生命周期 - 销毁完成
  activated  () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang='less' scoped>
// @import url(); 引入公共css类

</style>
